import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCallback, useState } from "react";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import OneOngoingAdmissionDetail from "./OneOngoingAdmissionDetail";
import { useAdmissionOngoingApplicationExport } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import TransferApplication from "./TransferApplication/TransferApplication";

const OneOngoingAdmissionWrapper = ({
  isExportAccess,
  isAddAccess,
  isTransferAccess,
  instituteId,
  applicationDetail,
  aid,
  children,
}) => {
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [admissionOngoingApplicationExport] =
    useAdmissionOngoingApplicationExport();

  const onExportList = () => {
    if (getQuery.state?.appId && isAuthorizedTab[isExportAccess]) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationExport({
        appId: applicationDetail?._id,
        flow: isExportAccess,
      })
        .then(() => {
          navigate(`/q/${params.username}/member/staff/admission?type=export`, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };
  const onJoinStudent = () => {
    if (
      instituteId &&
      getQuery.state?.appId &&
      applicationDetail?.applicationMaster &&
      applicationDetail?.applicationDepartment?._id &&
      applicationDetail?.applicationBatch?._id
    )
      navigate("/q/joining", {
        state: {
          formAs: "STUDENT",
          openAs: "ADMISSION_ADMIN_SIDE",
          instituteId: instituteId,
          appId: getQuery.state?.appId,
          admissionAmount: [
            applicationDetail?.admissionFee ?? 0,
            applicationDetail?.one_installments?.fees ?? 0,
          ],
          applicationMaster: applicationDetail?.applicationMaster,
          applicationDepartment: applicationDetail?.applicationDepartment?._id,
          batchId: applicationDetail?.applicationBatch?._id,
          departmentId: applicationDetail?.applicationDepartment?._id,
          whichFormShow: "DEPARTMENT",
        },
      });
  };

  const onCloseStep = () => {
    setStep("");
  };
  const onApplicationDetail = () => {
    setStep(1);
  };
  const onTransfer = () => {
    setStep(2);
  };
  const onNavigate = useCallback(() => {
    let url = "";
    if (isAuthorizedComplete[getQuery.state?.isAuthorization])
      url = `/q/${params.username}/member/staff/admission?type=complete`;
    else url = `/q/${params.username}/member/staff/admission?type=ongoing`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  // console.info("sd,fbfsn", step);
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{getQuery.state?.applicationName ?? ""}</h6>
          </div>

          <div className={style.fm_search}>
            <img
              src={`${assestsUserAdmissionUrl}/application-detail.svg`}
              alt="icon"
              title="Application details"
              onClick={onApplicationDetail}
            />
            {isAuthorizedTab[isAddAccess] &&
              !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <img
                  src={`${assestsUserAdmissionUrl}/add.svg`}
                  alt="icon"
                  title="Add student"
                  onClick={onJoinStudent}
                />
              )}
            {isAuthorizedTab[isExportAccess] && (
              <img
                src={`${assestsUserFinanceUrl}/export.svg`}
                alt="icon"
                title="List Export"
                onClick={onExportList}
              />
            )}
            {isAuthorizedTab[isTransferAccess] &&
              !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <img
                  src={`${assestsUserAdmissionUrl}/transfer.svg`}
                  alt="icon"
                  title="Transfer student"
                  onClick={onTransfer}
                />
              )}
            {children}
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={`/q/${params.username}/member/staff/admission?type=ongoing-detail${tab.urlKey}`}
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      {step === 1 ? (
        <OneOngoingAdmissionDetail
          applicationDetail={applicationDetail}
          onClose={onCloseStep}
        />
      ) : step === 2 ? (
        <TransferApplication
          onClose={onCloseStep}
          instituteId={instituteId}
          appId={applicationDetail?._id}
          aid={aid}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default OneOngoingAdmissionWrapper;

const tabList = [
  {
    label: "application",
    id: "application",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "docs_collection",
    id: "document",
    urlKey: "&n=document",
    groupIs: ["document"],
  },
  {
    label: "fees_collection",
    id: "fees",
    urlKey: "&n=fees",
    groupIs: ["fees"],
  },
  {
    label: "confirmed",
    id: "confirmed",
    urlKey: "&n=confirmed",
    groupIs: ["confirmed", "confirmed-multi"],
  },
  {
    label: "review",
    id: "review",
    urlKey: "&n=review",
    groupIs: ["review", "review-multi"],
  },
  {
    label: "allottment",
    id: "allottment",
    urlKey: "&n=allottment",
    groupIs: ["allottment"],
  },
  {
    label: "cancelled",
    id: "cancelled",
    urlKey: "&n=cancelled",
    groupIs: ["cancelled"],
  },
];
const isAuthorizedTab = {
  APPLICATION: true,
  Review: true,
  Request_Query: true,
  Confirm_Query: true,
  Allot_Query: true,
};
const isAuthorizedComplete = {
  isCompleted: true,
};
